body {
  background: linear-gradient(to left,
      rgba(7, 27, 82, 1) 0%,
      rgba(0, 128, 128, 1) 100%);
}

h1 {
  font-family: 'Bigelow Rules', cursive;
  font-size: 52px;
  color: #0ccac4;
  text-align: center;
}

.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: rgba(27, 112, 137, 1);
  border: 0;
  color: #ffffff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}

#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgba(202, 156, 24, 1);
  cursor: pointer;
  color: #ffffff;
  font-weight: bolder;
}

#to-do-form button:focus {
  outline: none;
}